import gql from 'graphql-tag'
import apolloClient from './graphql'

export function getVoteStatus(params) {
    return apolloClient.query({
        query: gql`query MyQuery($creator:String,$daoId:String,$proposal:String) {
            userProposalRecordEntities(
              where: {creator: $creator, daoId: $daoId, proposal: $proposal}
            ) {
              redeemStatus
            }
          }`,
        variables: params
    })
}
export function getVoteRecord(params) {
  return apolloClient.query({
      query: gql`query MyQuery($voter:String,$daoId:String,$proposal:String) {
        userProposalOptionRecordEntities(where: {daoId: $daoId, voter: $voter, proposal: $proposal}) {
          voter
          tokenAmount
          lpAmount
          amounts
          options
          status
        }
      }`,
      variables: params
  })
}
export function getMemberNumber(params) {
  return apolloClient.query({
      query: gql`query MyQuery($dao:String,$id:String) {
        proposalEntities(where: {dao: $dao, id: $id}) {
          votePerson
        }
      }`,
      variables: params
  })
}
